import React from "react";
import Navbar from "../src/components/Navbar";
import ServicesContainer from "../src/components/ServicesContainer";
import './App.css';
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import Home from "./pages/Home";
import CAREERS from "./pages/CAREERS";
import Tutoring from "./pages/Tutoring";
import English from "./pages/English";
import Evaluation from "./pages/Evaluation";


function App() {
  return (
    <div className="App">
      {/* <Navbar></Navbar>
      <header>
        <h1>Welcome to Refugee and Immigrant Services at Emily Griffith Technical College!</h1>
        <p>We offer a range of services to help you be successful...</p>
       
      </header>
     <ServicesContainer></ServicesContainer> */}
     <Router>
        <Switch>
          <Route exact path="/" component={Home} />
          <Route exact path="/careers/" component={CAREERS} />
          <Route exact path="/tutoring" component={Tutoring} />
          <Route exact path="/english" component={English} />
          <Route exact path="/evaluation" component={Evaluation} />

        </Switch>
      </Router>

    </div>
  );
}

export default App;



